import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			uid:0,
			salesOrder:'',
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			keyword:'',
			userInfo:[],
		};
	},
	
	onLoad(options){
		console.log('页面',options);
		this.uid = options.uid
		// this.keyword = options.keyword
		this.calcSize();
		getApp().user_api(true);
		this.my();
		this.getProductList();
		
	},
	onShow(){
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
	},
	//加载更多
	onReachBottom(){
		// this.loadData();
	},
	methods: {
		
		getProductList(){
			let self = this;
			let path = '/gzh/user_api/get_user_bill_spread_list'
			let uid = self.uid ? self.uid : ''
			
			let data = {
				page:self.page,
				limit:self.limit
			};
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
					// self.status = list.length <= self.limit ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
					// 	self.goodsList = self.goodsList.concat(list);
					// }
				} else {
					self.status = "";
				}
			})
		},
		
		my(){
			let self = this;
			let path = '/gzh/user_api/my'
			getApp().http(path, {}, function(res) {
				console.log('个人信息',res)
				self.userInfo = res;
			})
		},
		
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getProductList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					
					if(!self.uid){
						self.scrollHeight = res.windowHeight
					}else{
						self.scrollHeight = res.windowHeight
					}
					
				}
			});
		},
		
	},
}
